.slide-out {
  position: absolute;
  right: -100%;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  display: none;
}

.slide-out.opened {
  right: 0;
  opacity: 1;
  display: flex;
}

.slide-out .modal-close-btn {
  position: absolute;
  right: 80px;
  top: 80px;
  color: #fff;
  font-size: 52px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .slide-out {
    height: inherit;
    align-items: flex-start;
  }
  .slide-out .modal-close-btn {
    position: absolute;
    right: 25px;
    top: 35px;
    color: #fff;
    font-size: 32px;
    cursor: pointer;
    font-weight: 300;
    z-index: 5;
  }
}

