/* Hover poster */
.poster {
  transition: all 1s cubic-bezier(.05,1.27,1,.93);
}

.poster:hover {
  position: relative;
  transform: scale(1.25);
  z-index: 1;
  cursor: pointer;
  transition: all 1s cubic-bezier(.05,1.27,1,.93);
}

/* Masonry */
.my-masonry-grid { /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
  display: flex;
  margin-left: -30px; /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 30px; /* gutter size */
  background-clip: padding-box;
}
 
/* Style your items */
.my-masonry-grid_column > div { /* change div to reference your elements you put in <Masonry> */
  margin-bottom: 30px;
}

.my-masonry-grid_column > div img { /* change div to reference your elements you put in <Masonry> */
  width: 100%;
  -webkit-filter: drop-shadow(0px 14px 6px rgba(0, 0, 0, 0.7));
          filter: drop-shadow(0px 14px 6px rgba(0, 0, 0, 0.7));
}

@media (max-width: 768px) {
