/* Admin tools */
.admin-tools {
  position: fixed;
  left: 2.5rem;
  bottom: 2rem;
  z-index: 1;
}

.admin-tools .tools {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 10em;
  width: 75px;
  height: 225px;
  background: #fff;
  border-radius: 50px;
  opacity: 0;
  transition: all 0.5s;
}

.admin-tools .tools.active {
  opacity: 1;
  top: -15.5em;
  transition: all 0.5s;
}

.admin-tools .tools .tool {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: #eee;
